import React, { Component } from 'react';
import Slider from '@material-ui/core/Slider';
import {  Icon } from 'semantic-ui-react'
class ControlBar extends Component {
  constructor(props){
    super(props);
    console.log(props.updateSpeed);

  }

   marks = [
    {
      value: -10,
      label: '-10',
    },
    {
      value: 10,
      label: '10',
    },
  ];

   makeStyles = {
    root: {
      width: 300,
    },
  };

  



  render(){
    
    
    return(
      <div  className="Control-Bar" style={{width:'30%', margin:'auto'}}>
        <Slider
            min={-10}
            max={10}
            defaultValue={[0]}
            step={.1}
            valueLabelDisplay="auto"
            aria-labelledby="slider"
            marks={this.marks}
        />
          <Icon name='play'/>
          <Icon  name='pause'/>

        
      </div>
    );
  }
}

export default ControlBar;
